.show-data {
  overflow-y: scroll;
  width: 100%;
  height: 50vh;
  table {
    font-family: Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    color: black;
    width: 100%;
    thead {
      th {
        text-align: center;
      }
    }
    tbody {
      td:nth-child(3),
      td:nth-child(4) {
        text-align: center;
      }
    }
  }

  table td,
  table th {
    border: 1px solid #ddd;
    padding: 8px;
  }

  table tr:nth-child(even) {
    background-color: #f2f2f2;
  }

  table tr:hover {
    background-color: #ddd;
  }

  table th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: left;
    background-color: #04aa6d;
    color: white;
  }
}

.admin-update-button {
  margin: 20px auto;
}
